#topRightNavContainer {
  background-color: transparent !important;
  width: 100%;
}
.topRightNavLink {
  color: var(--blackColor) !important;
  margin-right: 3rem !important;
  font-size: 0.8rem;
  text-decoration: none;
  margin-top: 0.5em !important;

  /* background-color: red !important; */
}
.topRightNavLink:hover {
  color: var(--orangeColor) !important;
}
.topRightLink {
  color: var(--blackColor) !important;
  font-size: 0.8rem !important;
  margin-bottom: 0.6rem;
  text-decoration: none;
}
.topRightLink:hover {
  color: var(--orangeColor) !important;
}

#topRightDropdown {
  color: var(--blackColor) !important;
  margin-right: 2rem !important;
  font-size: 0.8rem;
  /* margin-bottom: 2em !important; */
  /* background-color: red !important; */
}
#topRightDropdown:hover {
  color: var(--orangeColor) !important;
}
a {
  text-decoration: none;
  color: var(--blackColor);
}
.navIcons {
  margin-right: 0.5em;
}
.sellIcon,
.profileIcon,
.messageIcon,
.logoutIcon {
  width: 2em;
}
.orderIcon {
  width: 1.5em;
}
.navLinks:hover {
  color: var(--orangeColor) !important;
}
.accountDiv {
  display: flex;
  position: relative;
}
.acctIconNav {
  width: 1.5em;
  margin: 0;
  cursor: pointer;
  position: absolute;
  top: 10%;
  left: -15%;
  border-radius: 50%;
}
.accontImgNav {
  width: 1.5em;
  margin: 0;
  cursor: pointer;
  position: absolute;
  top: 10%;
  left: -20%;
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
}
.accountDivSeller {
  margin-left: 3em;
  margin-right: 3em;
}

.helpSellerAcc {
  margin-right: 6em !important;
}
.cartCounter {
  display: flex;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: var(--orangeColor);
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 12%;
}
.cartCounter > span {
  font-size: 0.8em;
  color: white;
}
